.special-announcement {
  padding: 0px 32px 0px 32px;
}
@media only screen and (max-width: 768px) {
  .special-announcement {
    width: 100%;
    padding: 0px 0px 0px 0px;
  }
}
.special-announcement .info-annoucemnet {
  background-color: #e8dee2;
  border: 1px solid #c3a8b3;
}
.special-announcement .info-annoucemnet .title {
  font-family: 'Noto Serif', serif;
  font-size: 1.5rem;
  padding-bottom: 24px;
  margin-bottom: 24px;
  border-bottom: 1px solid #c3a8b3;
  color: #333;
  text-align: center;
}
@media only screen and (max-width: 768px) {
  .special-announcement .info-annoucemnet .title {
    font-size: 1.25rem;
    padding: 0 0;
    text-align: left;
  }
}
.special-announcement .ant-card-body {
  letter-spacing: 0.05em;
  font-family: 'Noto Sans', sans-serif;
  font-size: 0.9rem;
  line-height: 32px;
  font-weight: 200;
  padding: 24px 48px;
}
.special-announcement .ant-card-body .facebook-link {
  text-align: center;
}
.special-announcement .ant-card-body .team-announcement {
  text-align: right;
}
.brand-title {
  font-family: 'Noto Serif', serif;
  font-size: 2rem;
  padding: 12px 0px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 0em;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
}
.welcome-layout {
  font-family: 'Noto Sans', sans-serif;
  margin-top: 24px;
  margin-bottom: 24px;
  padding: 0 48px;
  background: transparent;
}
@media only screen and (max-width: 768px) {
  .welcome-layout {
    display: none;
  }
}
@media only screen and (max-width: 768px) {
  .welcome-layout {
    padding: 0 0;
    flex-direction: column;
  }
}
.welcome-layout .welcome {
  width: 100%;
}
.welcome-layout .welcome-sider {
  width: 300px;
}
@media only screen and (max-width: 768px) {
  .welcome-layout .welcome-sider {
    width: auto;
  }
}
.welcome-layout .ant-layout-sider {
  background: transparent;
}
.welcome-layout .welcome-content {
  padding: 12px 36px;
  letter-spacing: 0.05em;
  font-size: 0.9rem;
  line-height: 32px;
  font-weight: 200;
  background-color: transparent;
  text-align: center;
}
.welcome-layout .welcome-content .welcome-options {
  margin-right: 24px;
  background-color: #e8dee2;
  border: 1px solid #c3a8b3;
  letter-spacing: 0.05em;
}
.welcome-layout .welcome-content p {
  padding: 12px 0px;
  text-align: left;
}
.mobile-welcome-content {
  margin-top: 24px;
  text-align: center;
}
.mobile-welcome-content .mobile-welcome-pic {
  width: 300px;
}
.mobile-welcome-content .mobile-welcome-text {
  margin: 24px 12px 24px 24px;
  letter-spacing: 0.05em;
  font-size: 0.9rem;
  line-height: 32px;
  font-weight: 200;
  text-align: center;
  background-color: transparent;
}
.mobile-welcome-content .mobile-welcome-options {
  margin-bottom: 24px;
}
.mobile-welcome-content .mobile-welcome-options .mobile-option {
  font-family: 'Noto Serif', serif;
  background-color: #e8dee2;
  border: 1px solid #c3a8b3;
  letter-spacing: 0.05em;
}
